/* modal styles */
.backdrop{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0,0,0,0.5);
}

.backdrop .backdrop-img {
    display: block;
    max-width: 60%;
    max-height: 80%;
    margin: 60px auto;
    box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
    border: 3px solid white;
    overflow: hidden;
}

@media screen and (max-width: 974px) {
    .backdrop .backdrop-img {
        max-width: 80%;
        max-height: 80%;
    }
}
